
/*////////////NAVIGATION*/
/* Стилі для хедера */
.header {
    width: 100%;
    height: 70px; 
    
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
    backdrop-filter: blur(3px); 
    transition: background-color 0.3s ease; /* Плавний перехід кольору */
    color: #000;
}

.header__inner {
    display: flex;
    align-items: center;
    justify-content: space-between; /* Вирівнює елементи по краях */
    height: 100%;
    padding: 0 20px;
}

.nav__link__logo {
	margin: 0 25px;

	font-family: 'Open Sans', sans-serif;
	font-size: 35px;
	font-weight: 700;
	text-transform: none;

	color: #fff;
	text-decoration: none;
	opacity: .8;
	transition: .4s;
}

.nav__link__logo:hover {
	opacity: 1;
}


.nav {
    margin: 0 25px;
    display: flex;
    justify-content: flex-end; /* Вирівнює посилання по правій стороні */
    gap: 20px;
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
    font-weight: 1000;
}

.nav__link {
    margin-right: 30px;
    font-size: 20px;
    color: #fff;
    text-decoration: none;
    opacity: 0.8;
    transition: 0.2s;
}

.nav__link1 {
	text-decoration: none;
	color: #000;
        
	opacity: .6;
	transition: .2s;
}

/* Основний контент */
.main-content {
    padding-top: 0px; /* Збільшуємо відступ, щоб контент не перекривався хедером */
}


.nav__link:hover {
	text-decoration: none;
    color: #fff;
	opacity: 1;
}



.nav__link1:hover {
	text-decoration: none;
	opacity: 1;
}